.main {
    padding-top: 120px;
    
    a {
        text-decoration: none;
    }

    h1 {
        font-family: 'Montserrat', sans-serif;
        font-style: normal;
        font-weight: 200;
        font-size: 24px;
        line-height: 29px;
        color: #34495E;
        margin-left: 20px;
    }

    &__box {

        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;

        &-cart {
            width: 180.01px;
            height: 256.92px;
            box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
            border-radius: 0px 0px 18px 18px;
            margin-top: 25px;

            img {
                width: 180.01px;
                height: 180.01px;
            }

            &-pricebox {
                width: 180px;
                height: 57px;
                background: #C4C4C4;
                border-radius: 0px 0px 18px 18px;
                display: flex;
                justify-content: space-around;
                align-items: center;

                &-price {
                    font-family: 'Montserrat', sans-serif;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 20px;
                    line-height: 24px;

                    a {
                        color: #000000;
                    }
                }
            }

            &-description {
                font-style: normal;
                font-weight: bold;
                font-size: 12px;
                line-height: 15px;
                color: #000000;
                font-family: 'Montserrat', sans-serif;
            }

            ion-icon {
                font-size: 32px;
                color: #000000;
            }
        }
    }
}