html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.header {
  background-color: rgba(229, 233, 224, 0.788);
  height: 100%;
}

.header a {
  text-decoration: none;
  color: black;
}

.header-ul {
  background-color: #000;
  height: 75px;
  max-width: 500px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
}

.header-ul-li {
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.header-ul-li-menu {
  font-size: 65px;
  color: white;
  margin-left: 10px;
  cursor: pointer;
}

.header-ul-li-input {
  border-radius: 10px 0px 0px 10px;
  width: 100%;
  max-width: 200px;
  height: 30px;
}

.header-ul-li-cart {
  font-size: 50px;
  color: white;
  margin-right: 10px;
}

.header-ul-li button {
  width: 50px;
  height: 30px;
  border-radius: 0px 10px 10px 0px;
}

.header-ul-li div {
  display: flex;
}

.header__wraper {
  background-color: white;
  height: 100%;
  min-height: 736px;
  width: 284px;
  position: absolute;
  z-index: 9;
  display: none;
}

.header__wraper--active {
  display: block;
}

.header__wraper-heder {
  height: 117px;
  background-color: black;
  width: 284px;
}

.header__wraper-heder-logobox {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.header__wraper-heder-logobox ion-icon {
  font-size: 50px;
  color: white;
}

.header__wraper-heder-callbox {
  display: flex;
  padding: 10px;
}

.header__wraper-heder-callbox a {
  text-decoration: none;
  color: white;
}

.header__wraper-heder-callbox ion-icon {
  font-size: 20px;
  color: white;
}

.header__wraper-main span {
  border: 1px solid black;
  width: 283px;
  display: flex;
}

.header__wraper-main-ul {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  height: 50px;
  margin-left: 15px;
  display: flex;
  justify-content: space-between;
  font-family: 'Montserrat', sans-serif;
}

.header__wraper-main-ul-li {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.header__wraper-main-ul-li ion-icon {
  font-size: 30px;
  margin-right: 15px;
}

.header__wraper-main-ul-li-submenu {
  display: none;
}

.header__wraper-main-ul-li-submenu--active {
  display: block;
}

.header__wraper-footer {
  min-height: 380px;
  display: flex;
  justify-content: end;
  flex-direction: column;
}

.header__wraper-footer span {
  border: 1px solid black;
  width: 283px;
  display: flex;
}

.header__wraper-footer a {
  height: 50px;
  justify-content: center;
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}

.main {
  padding-top: 120px;
}

.main a {
  text-decoration: none;
}

.main h1 {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 24px;
  line-height: 29px;
  color: #34495E;
  margin-left: 20px;
}

.main__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.main__box-cart {
  width: 180.01px;
  height: 256.92px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
  border-radius: 0px 0px 18px 18px;
  margin-top: 25px;
}

.main__box-cart img {
  width: 180.01px;
  height: 180.01px;
}

.main__box-cart-pricebox {
  width: 180px;
  height: 57px;
  background: #C4C4C4;
  border-radius: 0px 0px 18px 18px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.main__box-cart-pricebox-price {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
}

.main__box-cart-pricebox-price a {
  color: #000000;
}

.main__box-cart-description {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
  font-family: 'Montserrat', sans-serif;
}

.main__box-cart ion-icon {
  font-size: 32px;
  color: #000000;
}

.footer {
  width: 414px;
  height: 400px;
  background: #262626;
  margin-top: 70px;
}

.footer a {
  text-decoration: none;
}

.footer__onecontainer h1 {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
  text-align: center;
  padding-top: 8px;
}

.footer__onecontainer-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.footer__onecontainer-box-cart {
  width: 185.71px;
  height: 279px;
  background: #C4C4C4;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  margin-left: 7px;
  margin-top: 25px;
}

.footer__onecontainer-box-cart img {
  width: 185.71px;
  height: 184.86px;
}

.footer__onecontainer-box-cart-pricebox {
  text-align: center;
}

.footer__onecontainer-box-cart-pricebox a {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}

.footer__onecontainer-box-cart-description {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
  font-family: 'Montserrat', sans-serif;
  padding: 5px;
}

.footer__twocontainer {
  background: #1a1a1a;
  height: 139px;
  width: 100%;
  margin-top: 59px;
  display: flex;
}

.footer__twocontainer-logobox {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.footer__twocontainer-descriptionbox {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  color: #FFFFFF;
  font-family: 'Montserrat', sans-serif;
}

.footer__twocontainer-descriptionbox a {
  color: #FFFFFF;
}
