

.header {
    background-color: rgba(229, 233, 224, 0.788);
    height: 100%;

    a {
        text-decoration: none;
        color: black;
    }

    &-ul {
        background-color: #000;
        height: 75px;
        max-width: 500px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: absolute;

        &-li {
            flex-direction: column;
            display: flex;
            justify-content: center;

            &-menu {
                font-size: 65px;
                color: white;
                margin-left: 10px;
                cursor: pointer;
            }

            &-input {
                border-radius: 10px 0px 0px 10px;
                width: 100%;
                max-width: 200px;
                height: 30px;
            }

            &-cart {
                font-size: 50px;
                color: white;
                margin-right: 10px;
            }

            button {
                width: 50px;
                height: 30px;
                border-radius: 0px 10px 10px 0px;
            }

            div {
                display: flex;
            }
        }
    }

    &__wraper {
        background-color: white;
        height: 100%;
        min-height: 736px;
        width: 284px;
        position: absolute;
        z-index: 9;
        display: none;

        &--active {
            display: block;
        }

        &-heder {
            height: 117px;
            background-color: black;
            width: 284px;

            &-logobox {
                display: flex;
                justify-content: space-between;
                padding: 10px;

                ion-icon {
                    font-size: 50px;
                    color: white;
                }
            }

            &-callbox {
                display: flex;
                padding: 10px;

                a {
                    text-decoration: none;
                    color: white;
                }

                ion-icon {
                    font-size: 20px;
                    color: white;
                }
            }
        }

        &-main {

            span {
                border: 1px solid black;
                width: 283px;
                display: flex;
            }

            &-ul {
                font-weight: 600;
                font-size: 18px;
                line-height: 22px;
                height: 50px;
                margin-left: 15px;
                display: flex;
                justify-content: space-between;
                font-family: 'Montserrat', sans-serif;

                &-li {
                    display: flex;
                    justify-content: center;
                    flex-direction: column;

                    ion-icon {
                        font-size: 30px;
                        margin-right: 15px;
                    }
                    &-submenu {
                        display: none;
                    }
                    &-submenu--active {
                        display: block;
                    }
                }
            }
        }

        &-footer {
            min-height: 380px;
            display: flex;
            justify-content: end;
            flex-direction: column;

            span {
                border: 1px solid black;
                width: 283px;
                display: flex;
            }
            a {
                height: 50px;
                justify-content: center;
                display: flex;
                flex-direction: column;
                margin-left: 15px;
            }
        }
    }
}