.footer {
    width: 414px;
    height: 400px;
    background: #262626;
    margin-top: 70px;

    a {
        text-decoration: none;
    }

    &__onecontainer {

        h1 {
            font-family: 'Montserrat', sans-serif;
            font-style: normal;
            font-weight: 200;
            font-size: 24px;
            line-height: 29px;
            color: #FFFFFF;
            text-align: center;
            padding-top: 8px;
        }

        &-box {

            display: flex;
            flex-wrap: wrap;
            justify-content: center;
    
            &-cart {
                width: 185.71px;
                height: 279px;
                background: #C4C4C4;
                box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
                margin-left: 7px;
                margin-top: 25px;
    
                img {
                    width: 185.71px;
                    height: 184.86px;
                }
                
                &-pricebox {
                    text-align: center;

                    a {
                        text-align: center;
                        font-family: 'Montserrat', sans-serif;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 20px;
                        line-height: 24px;
                        color: #000000;
                    }
                }
    
                &-description {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 12px;
                    line-height: 15px;
                    color: #000000;
                    font-family: 'Montserrat', sans-serif;
                    padding: 5px;
                }
            }
        }
    }
    &__twocontainer {
        background: #1a1a1a;
        height: 139px;
        width: 100%;
        margin-top: 59px;
        display: flex;

        &-logobox {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        &-descriptionbox {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            font-style: normal;
            font-weight: 500;
            font-size: 10px;
            line-height: 12px;
            color: #FFFFFF;
            font-family: 'Montserrat', sans-serif;

            a {
                color: #FFFFFF;
            }
        }
    }
}